<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>市场管理</BreadcrumbItem>
      <BreadcrumbItem>货物运价</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Row class="row">
        <Button type="primary" @click="addGoods">添加货种</Button>
        <Modal
          v-model="showModal"
          title="添加货种信息"
          @on-ok="ok"
          @on-cancel="cancel"
          width='900'
        >
        <Row class="row">
            <Col span="3">货种</Col>
            <Col span="10"> <Input placeholder="请输入货种" style="width:240px" v-model="cargo"/></Col>
        </Row>
        <Row class="row" v-for="(item,index) in lineArr" :key='index'>
            <Col span="3">航线</Col>
            <Col span="7">
              <Input placeholder="请输入起始航线" style="width:100px" v-model="item.loadPort" />  <span style="margin:0 10px">至</span>
              <Input placeholder="请输入结束航线" style="width:100px" v-model="item.discPort"/>
            </Col>
            <Col span="1">运价</Col>
            <Col span="5"> <Input placeholder="请输入运价" style="width:100px" v-model="item.freight"/></Col>
            <Col span="1">涨幅</Col>
            <Col span="3"> <Input placeholder="请输入涨幅" style="width:100px" v-model="item.p"/></Col>
            <Col span="2">
               <Icon type="md-add-circle" style="font-size:20px" @click="addPlus"  v-if="index == 0" />
               <Icon type="ios-remove-circle" style="font-size:20px" @click="minusPlus(index)"  v-if="index" />
            </Col>
        </Row>

        </Modal>
      </Row>
       <Collapse  accordion style="margin-top:20px;width:100%"  @on-change='change'>
        <Panel  :name='list.value' v-for="(list,index) in goodsInfo" :key='index'   ref="Panelkey">
           <span>{{list.cargo}}</span>
            <Row slot="content" class="row">
              <Col span="1">航线</Col>
              <Col span="8">
                <Input style="width:120px" placeholder="起始航线" v-model="list.starLine" />
                <span>至</span> <Input style="width:120px" v-model="list.endLine" placeholder="结束航线" />
              </Col>
              <Col span="1">运价</Col>
              <Col span="3"> <Input style="width:120px" placeholder="请输入运价"  v-model="list.price" /></Col>
              <Col span="1">涨幅</Col>
              <Col span="3"> <Input style="width:120px" placeholder="请输入涨幅"  v-model="list.zFu"  /></Col>
              <Col span="1"> <Button type="primary"  @click="saveAdd(index,list.cargo)">保存</Button>  </Col>
          </Row>
           <Row slot="content">
             <table class="table table-row" style="width:100%">
                 <tr>
                     <td>航线</td>
                     <td>运价</td>
                     <td>涨幅</td>
                     <td>日期</td>
                     <td>操作</td>
                 </tr>
                 <tr v-for="(item,indx) in list.marketFreights" :key="indx">
                     <td v-if="!item.isEditor">{{item.airway}}</td>
                     <td v-else>
                        <Input style="width:80px" placeholder='起始航线' v-model="item.starLine" />
                      <span style="margin:0 10px">至</span>
                      <Input style="width:80px" placeholder="结束航线" v-model="item.endLine" />
                     </td>
                     <td v-if="!item.isEditor">{{item.freight}}</td>
                     <td v-else> <Input style="width:70px" placeholder="请输入运价" v-model="item.freight" /></td>
                     <td v-if="!item.isEditor">{{item.priceGain}}</td>
                     <td v-else> <Input style="width:70px" placeholder="请输入涨幅" v-model="item.priceGain" /></td>
                     <td >{{item.createTime}}</td>
                     <!-- <td v-else>
                       <DatePicker type="date" placeholder="请选择日期" style="width: 120px" format="yyyy/MM/dd" v-model="item.date" @on-change="handleDate($event,index,indx,'date')" ></DatePicker>
                       <TimePicker type="time" placeholder="请选择时间" style="width: 120px" v-model="item.time"   @on-change="handleDate($event,index,indx,'time')" ></TimePicker>
                     </td> -->
                     <td v-if="!item.isEditor"><Button type="primary" size='small' @click="handleEditor(index,indx)">修改</Button> </td>
                     <td v-else><Button type="primary" size='small' @click="save(index,indx,item.id,list.cargo)">保存</Button> </td>
                 </tr>
             </table>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
export default {
  data() {
    return {
      cargo:'',
      Panelkey:'',
      value1: "1",
      showModal: false,
      lineArr:[
        {
          loadPort:'',
          discPort:'',
          freight:'',
          p:''
       }
      ],
      goodsInfo:[],
      starLine:'',
      endLine:'',
      price:"",
      zFu:'',
    };
  },
  methods: {
    addGoods() {
      this.showModal = true;
    },
    async ok() {
      let init = [
        {
          loadPort:'',
          discPort:'',
          freight:'',
          p:''
       }
      ]
     let data = {
       cargo:this.cargo,
       marketFreight:[...this.lineArr]
     }
     try{
       let res = await this.$http.addCargo(data)
       if(0==res.data.status){
         this.$Notice.success({
           desc:"添加成功"
         })
          this.cargo = ''
          this.lineArr = init
          this.getCargoInfo(1000)
       }
       else{
          this.$Notice.error({
           desc:res.data.msg
         })
       }

     }
     catch(err){
        console.log(err)
     }
    },
    cancel() {
      // this.$Message.info("Clicked cancel");
        this.cargo = ''
        this.lineArr = [{
          loadPort:'',
          discPort:'',
          freight:'',
          p:''
       }]
    },
    addPlus(){
      let obj = {
        loadPort:'',
        discPort:'',
        freight:'',
        p:''
      }
      this.lineArr.push(obj)
    },
    minusPlus(index){
        this.lineArr.splice(index,1)
    },
    // initType ==1 定义初始化 获取货种详情
    //  initType !=1 其他操作 要调取getCargoInfo 这个fn
    async getCargoInfo (initType =1000){

        let res = await this.$http.getCargoInfo()
        this.goodsInfo = []
        var result = res.data.result
          if(0==res.data.status) {
            for(let i in result){
              for(let j in result[i]['marketFreights']){
                result[i]['marketFreights'][j].isEditor = false
                result[i]['marketFreights'][j].starLine =   result[i]['marketFreights'][j].airway ? result[i]['marketFreights'][j].airway.split('-')[0]:''
                result[i]['marketFreights'][j].endLine = result[i]['marketFreights'][j].airway ? result[i]['marketFreights'][j].airway.split('-')[1]:""
                result[i]['marketFreights'][j].date = result[i]['marketFreights'][j].createTime ? result[i]['marketFreights'][j].createTime.split(' ')[0]:""
                result[i]['marketFreights'][j].time = result[i]['marketFreights'][j].createTime ? result[i]['marketFreights'][j].createTime.split(' ')[1]:""
              }
            }
            this.goodsInfo = []
            result.map((item,index)=>{
                  this.goodsInfo.push(
                      Object.assign({},item,{
                        value:index+1+'',
                        starLine:"",
                        endLine:'',
                        zFu:'',
                        price:'',
                        isCollapse:false
                      })
                  )
            })
              if(1000 !=initType){
                 this.goodsInfo[initType]['isCollapse'] = true
                 this.goodsInfo[initType]['value'] = initType+1+''
                  var Panelkey = this.$refs.Panelkey
                //  for( let  i =0; i<Panelkey.length;i++){
                //    }
                  Panelkey[initType].isActive = true
                //  console.log(Panelkey)

              }
          }
          else{
            this.$Notice.error({
              desc:'暂无数据'
            })
          }
        // console.log( this.goodsInfo)

    },
    // 编辑
    handleEditor(outIndex,index){
      this.goodsInfo[outIndex]['marketFreights'][index]['isEditor'] = true
    },
    // 保存
  async  save(outIndex,index,id,cargo){
      let {time,endLine,starLine,freight,priceGain} = this.goodsInfo[outIndex]['marketFreights'][index]
        let data  = {
          cargo,
          marketFreightId:id,
          route:starLine+'-'+endLine,
          freight,
          priceGain
        }
        try{
          let res = await this.$http.updateCargoInfoByCargo(data)
          if(0==res.data.status){
            this.$Notice.success({
              desc:'修改成功'
            })
             this.getCargoInfo(outIndex)
          }else{
             this.$Notice.error({
              desc:res.data.msg
            })
            return false
          }

        }
        catch(err){
            this.$Notice.error({
              desc:'捕获异常.'
            })
            throw Error(err)
        }
    },
    // 处理日期
    handleDate(event,outIndex,index,params){
       if(params == 'date'){
          this.goodsInfo[outIndex].marketFreights[index].date= event
       }
       else{
         this.goodsInfo[outIndex].marketFreights[index].time= event
       }
    },
    // 在货种中新加一条记录
   async saveAdd(index,cargo){
      let {starLine,endLine,zFu,price} = this.goodsInfo[index]
      let formData = {
        loadPort:starLine,
        discPort:endLine,
        freight:price,
        priceGain:zFu,
        cargo
      }
      // console.log(formData)
        try{
          let res = await this.$http.addCargoInfoByCargo(formData)
          if(0==res.data.status){
            this.$Notice.success({
              desc:'保存成功'
            })
             this.getCargoInfo(index)
          }else{
             this.$Notice.error({
              desc:res.data.msg
            })
            return false
          }

        }
        catch(err){
            this.$Notice.error({
              desc:'捕获异常.'
            })
            throw Error(err)
        }
    },
    change(e){
      // console.log(e,'cahnge')
      e.join(',')
    }

  },
  mounted(){
        this.getCargoInfo()
  }
};
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.row{
  line-height: 45px;
}
.table, tr ,td{
  border: 1px solid #e4e4ee;
  border-collapse: collapse;
  padding: 8px 50px;
  text-align: center;
}
</style>



